*{
    margin:0;
    padding: 0;
    box-sizing: border-box;
    font-family: sans-serif;
}
.container,.containers{
    height: 100vh;
    background: teal;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}
.container .wraper,.wrapers{
    min-height: 100px;
    min-width: 30%;
    padding:5.1px ;
    display: flex;
    align-items: center;
    position: relative;
    flex-wrap: wrap;
    justify-content: space-evenly;
    background-color: rgb(5, 150, 150);
    box-shadow: 1px 1px 25px 5px rgb(37, 103, 113);
    /* rgb(32, 146, 139) */
    border-radius: 10px;
}
.container .wraper input,.containers .wrapers input{
    width: 55%;
    height: 30px;
    outline: none;
    border: none;
    font-size: 15px;
    padding:15px 10px;
    caret-color: gray;
    border-radius: 5px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;

}
.container .wraper button,.containers .wrapers button,.btn{
    height: 30px;
    width: 30%;
    border: none;
    outline: none;
    cursor: pointer;
    font-size: 17px;
    border-radius:5px ;
    position: relative;
    overflow: hidden;
    color: #000;
   
    z-index: 2;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.container .wraper button::after,.containers .wrapers button::after,.btn::after{
    content: '';
    position: absolute;
    transition: .2s ease-in-out;
    top: 0;
    left: 0;
    height: 100%;
    width: 0%;
    z-index: -1;
    background:radial-gradient(rgb(211, 196, 235),rgb(108, 202, 202));
    
}
.container .wraper button:hover::after,.containers .wrapers button:hover::after,.btn:hover::after{
    width: 100%;
}
.containers{
    background: linear-gradient(to right,#170E13,#7A7ADB);
    color: azure;
}
.containers .wrapers{
    background: rgb(65, 65, 114);
    box-shadow: 1px 1px 10px 10px #686889;
    transition: all 0.8s ease;
}
/* .containers .wrapers:hover{
    transform: scale(1.1);
} */
.containers .wrapers button:hover::after{
    background:#7A7ADB ;
    width: 100%;
}
@media(max-width:990px){
    .container .wraper,.wrapers{
        width: 60%;
    }
}
@media(max-width:500px){
    .container .wraper,.wrapers{
        width: 80%;
    }

}
@media(max-width:800px){
    h1{
        font-size: 25px;
        font-family: sans-serif;
    }
}
@media(max-width:400px){
    .container .wraper,.wrapers{
        width: 90%;
    }
    h1{
        font-size: 20px;
        font-family: sans-serif;
    }
}
.btn{
    width: 100px;
    position: absolute;
    bottom: 50px;
    right: 50px;
}